<template>
  <div id="app">
    <div class="content">
      <div class="talking">
        <ul class="talkul">
          <li v-for="data in datas" class="li">
            <div class="img"></div>
            <h1 class="name"></h1>
            <div class="talkcontent">
                <p class="text">{{data.text}}</p>
            </div>
          </li>
        </ul>
      </div>
      <el-input placeholder="input something" style="width: 80%;margin-top: 30px;" v-model="text" class="eli"></el-input>
      <el-button type="primary" plain style="width: 19%;margin-top: 20px;" @click="submit" class="elb">SUBMIT</el-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'App',
  data(){
    return {
      text:'',
      datas:[]
    }
  },
  methods:{
    submit(){
      axios.post('/talk.json',{
        text:this.text
      }).then((data)=>{
        axios.get('/talk.json').then((data)=>{
          this.datas=data.data;
          
        }).then(()=>{
          var e=document.getElementsByClassName("talking")[0];
          e.scrollTop=e.scrollHeight;
        })
      })
    }
  },
  created() {
    axios.get('/talk.json').then((data)=>{
      this.datas=data.data;
      console.log(this.datas);
      
    })
  }
}
</script>

<style>
  *{
    padding: 0px;
    margin: 0px;
  }
.content{
  width: 60%;
  height:800px;
  margin: auto;
  border: 1px solid rgb(200,200,200);
  padding: 50px;
  border-radius: 10px;
}
.talking{
  width: 100%;
  height:750px;
  overflow: auto;
}
.talkul{
  width: 80%;
  height:auto;
  list-style: none;
  
}
.talkul li{
  width: 100%;
  height:auto;
  margin-top: 50px;
}
.img{
  width: 50px;
  height:50px;
  border-radius: 100%;
  background-color: rgb(13,188,121);
}
.talkcontent{
  width: 100%;
  height: auto;

}
.li{
  animation: go 500ms;
  
}
p{
  margin-top: 20px;
}

@media screen and (max-width:500px){
  .content{
    margin-top: 20px;
    width: 80%;
    height:450px;
    padding: 20px;
  }
  .talking{
    height:400px;
  }
  .eli{
    width: 68% !important;
    margin-top: 10px !important;
  }
  .elb{
    width: 30% !important;
  }
}

@keyframes go{
  from{
    transform: translateX(100px);
    opacity: 0;
  }
  to{
    transform: translateX(0px);
    opacity: 1;
  }
}
</style>



